.basicStyle,
.topBarOuter,
.topBarOuter .left,
.topBarOuter .left .titleOuter,
.topBarOuter .left .menu,
.topBarOuter .right {
  height: 100%;
  background-color: #001529;
}
.basicFlex,
.topBarOuter,
.topBarOuter .left,
.topBarOuter .left .titleOuter,
.topBarOuter .left .menu,
.topBarOuter .left .menu .menuInner,
.topBarOuter .right {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.topBarOuter {
  padding: 0 1rem;
}
.topBarOuter .left .titleOuter .logo {
  background: url(../../assets/img/logo.png) no-repeat center 0vw/2.7vw;
  background-size: cover;
}
.topBarOuter .left .menu .menuInner {
  justify-content: center;
}
.topBarOuter .left .menu .active {
  background-color: #1890ff;
  transition: 300ms;
}
.topBarOuter .right .logout {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
